.qa-item-edit-mode__buttons > button {
    width: 42px;
    height: 38px;
}

.qa-item-edit-mode__title {
    display: flex;
    word-break: break-word;;
}

.qa-item-edit-mode__buttons img {
    margin-left: -4px;
    margin-top: -3px;
}

.qa-item-edit-mode__question-label {
    display: block;
}

.qa-item-edit-mode__question--invalid {
    border-color: rgb(245, 194, 199);
    background-color: #f8d7da;
}

.qa-item-edit-mode__btns-checkbox {
    display: flex;
    flex-direction: column;
    margin-left: auto;
}

.qa-item-edit-mode__btns-checkbox .qa-tool-checkbox__label {
  font-size: small;
}