.qa-manager {
    display: flex;
    flex-direction: column;
    min-height: 1px;
    padding: 0 5px 5px 0;

    &__ul-container {
        overflow: auto;
    }

    &__header {
        margin-bottom: 20px;
    }

    &__buttons {
        display: flex;
        padding: 10px 0 0 0;
    }

    &__button-remove-all {
        margin-right: auto;
    }

    &__simple-bar-container {
        overflow: hidden;

        > div {
            height: 100%;
        }
    }

    &__create-new-container {
        padding: 10px;
        border-style: solid;
        border-width: 1px;
        border-color: rgba(0, 0, 0, 0.125);
        border-bottom-right-radius: 0.375rem;
        border-bottom-left-radius: 0.375rem;
        background-color: #eee;
    }

    &__warning {
        background-color: #f8d7da;
        color: #842029;;
        border: 1px solid #f5c2c7;
        border-top-right-radius: 0.375rem;
        border-top-left-radius: 0.375rem;
        padding: 10px;
    }

    &__button-create-new {
        @media only screen and (max-width: 370px) {
            > label {
                display: none !important;
            }
            > i {
                display: block !important;
            }
        }
    }

    li {
        padding: 0px;
    }
}

.no-touch .qa-manager {
    &__button-create-new {
        > label {
            display: block;
        }
        > i {
            display: none;
        }
    }

    li:last-child {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }
}

.touch {
    .qa-manager {
        flex-direction: column-reverse;

        &__warning {
            border-top-left-radius: 0;
            border-top-right-radius: 0;
            border-bottom-left-radius: 0.375rem;
            border-bottom-right-radius: 0.375rem;
        }

        &__header-container {
            order: 1
        }

        &__buttons {
            flex-direction: row-reverse;
            padding: 0 0 10px 0;

            > button:not(:first-child) {
                margin-left: 0px;
                margin-right: 5px;
            }
        }

        &__button-remove-all {
            margin-right: 5px;
            margin-left: auto !important;
        }

        &__create-new-container {
            border-radius: 0;
            border-top-right-radius: 0.375rem;
            border-top-left-radius: 0.375rem;
        }

        &__button-create-new {
            > label {
                display: none;
            }
            > i {
                display: block;
            }
        }

        li:first-child {
            border-top-left-radius: 0;
            border-top-right-radius: 0;
        }
    }
}